header.banner {
  padding: 4px 0;
}

header.banner a {
  font-size: 0.9375rem;
}

header.banner .banner-title {
  width: auto;
  margin-top: 2px;
}

header.banner .banner-title .portal-logo {
  margin: 0;
}

header.banner .banner-title .portal-img {
  height: auto;
}

header.banner .banner-title .portal-img img {
  width: 108px;
}

header.banner .banner-title h1 {
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute !important;
  width: 1px;
  word-wrap: normal !important;
}

header.banner .banner-title h1:focus {
  background-color: #eee;
  clip: auto !important;
  clip-path: none;
  color: #444;
  display: block;
  font-size: 1em;
  height: auto;
  left: 5px;
  line-height: normal;
  padding: 15px 23px 14px;
  text-decoration: none;
  top: 5px;
  width: auto;
  z-index: 100000;
}

header.banner .banner-nav {
  width: auto;
}

header.banner .banner-nav .welcome,
header.banner .banner-nav .welcome b {
  display: none;
}

header.banner .banner-nav {
  margin: 0;
}

header.banner .banner-nav b {
  vertical-align: middle;
}

header.banner .banner-nav .caret {
  display: none;
}

header.banner .banner-nav .banner-language-selector {
  margin: 0 0 0 10px;
}

header.banner .banner-nav .banner-language-selector .dropdown-toggle {
  font-weight: 400;
  color: #fff;
  border: 1px solid rgba(255, 255, 255, 0.35);
  border-radius: 2px;
  height: 24px;
  width: 75px;
  padding: 0;
  font-size: 13px;
  background: none;
  text-align: center;
}

header.banner .banner-nav .banner-language-selector ul.dropdown-menu {
  top: 0;
  right: 0;
  left: 0;
  border-radius: 2px;
}

nav.page-tabs .nav-link {
  margin: 0;
}

nav.page-tabs .nav-link a {
  font-weight: 400;
}

nav.page-tabs .nav-link a.active {
  border-top: 0;
  border-bottom: 3px solid #002248;
  padding: 6px 6px 3px 6px;
  font-weight: 700;
}

nav.page-tabs .nav-link a:hover {
  border-top: 0;
  border-bottom: 3px solid rgba(0, 35, 70, 0.5);
  padding: 6px 6px 3px 6px;
  font-weight: 500;
}

nav.page-tabs .nav-link a.active:hover {
  border-color: #002248;
}

footer.footer {
  background-color: #fdfdfe;
  padding: 0 50px 54px;
  border: 0;
  color: #fff;
  margin: 0;
  border-radius: 0;
  border-top: 1px solid #cacaca;
}

footer.footer ul, footer.footer li {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

footer.footer nav {
  max-width: 1000px;
  margin: 30px auto 0;
  height: 30px;
  align-items: center;
  padding-top: 10px;
  padding-bottom: 10px;
  max-width: 71.25rem;
  padding-top: 10px;
  padding-bottom: 10px;
  min-height: 78%;
  display: flex;
}

footer.footer a {
  margin: 0 !important;
}

footer nav {
  display: flex;
}

footer nav .internal-links {
  flex: 1;
  display: flex;
}

footer nav .internal-links li {
  line-height: 15px;
  padding: 0 15px;
}

footer nav .internal-links li:first-child {
  padding-left: 0;
}

footer nav .internal-links li:last-child {
  padding-right: 0;
}

footer nav .external-links {
  display: flex;
  justify-content: flex-end;
}

footer nav .external-links li {
  margin-left: 23px;
}

footer nav .external-links a.ci {
  text-indent: -999em;
  overflow: hidden;
  display: inline-block;
  width: 20px;
  height: 20px;
  background-repeat: no-repeat;
  background-color: transparent;
  background-position: center center;
  background-size: contain;
}

footer nav .external-links a.ci.twitter {
  background-image: url("ci-twitter@2x.png");
}

footer nav .external-links a.ci.facebook {
  background-image: url("ci-facebook@2x.png");
}

footer nav .external-links a.ci.telegram {
  background-image: url("ci-telegram@2x.png");
}

footer .company-name > div {
  max-width: 71.25rem;
  margin: 30px auto 0;
}

footer .administrative-info {
  max-width: 71.25rem;
  margin: 7px auto 0;
}

footer .copyright, footer .person-info {
  max-width: 71.25rem;
  margin: 6px auto 0;
}

footer .info-items {
  display: flex;
}

footer .info-items li {
  padding: 0 15px;
}

footer .info-items li:first-child {
  padding-left: 0;
}

footer .info-items li:last-child {
  padding-right: 0;
}

footer nav .links {
  cursor: pointer;
}

footer nav .internal-links li {
  font-size: 13px;
  border-right: 1px solid #cacaca;
}

footer nav .internal-links li,
footer nav .internal-links li a,
footer nav .internal-links li a:visited {
  letter-spacing: -1px;
  color: #666;
}

footer nav .internal-links li a:active,
footer nav .internal-links li a:hover {
  color: #222;
  letter-spacing: -1px;
  text-decoration: underline;
}

footer nav .internal-links li:last-child {
  border-right: none;
}

footer .company-name a, footer .company-name a:visited {
  font-size: 13px;
  font-weight: 700;
  color: #777;
  letter-spacing: -1px;
}

footer .info-items li {
  color: #777;
  font-size: 11px;
  border-right: 1px solid #cacaca;
  letter-spacing: -.8px;
}

footer .info-items li:last-child {
  border-right: none;
}

footer .copyright {
  font-size: 12px;
  color: #888;
}

.footer {
  background-color: #fff;
}

@media (min-width: 45em) {
  .footer-links .privacy-link {
    float: right;
    margin-left: 25px;
  }
  .footer-links .cookie-link {
    float: right;
  }
  .footer-links .cookie-link:before {
    content: "";
  }
}

@media (max-width: 45em) {
  .footer {
    margin-top: 0;
    padding: 1em .5em;
  }
  .footer a {
    padding-left: 10px;
    padding: 0 10px;
    margin-right: 4px;
    margin-left: 4px;
    line-height: 24px;
    display: inline-block;
    margin-bottom: 10px;
  }
  .footer a,
  .footer a:hover {
    color: #183247;
  }
  .footer-links {
    text-align: center;
  }
  .footer-links a:before {
    content: none;
  }
}

footer .copyright {
  padding: 0;
  background: transparent;
  text-align: left;
}

.page section.help-center {
  text-align: center;
}

.page section.help-center.home {
  border-radius: 0;
  padding: 40px 0;
}

.page section.help-center.home h2.heading {
  font-size: 2rem;
  line-height: 1;
  color: #002248;
  text-shadow: 0 0 4px rgba(0, 34, 72, 0.4);
}

.page section.help-center.home .hc-search {
  width: 60%;
  padding-right: 0;
}

.page section.help-center.home .hc-search .hc-search-form {
  width: inherit;
  text-align: center;
}

.page section.help-center.home .hc-search .hc-search-form:focus-within {
  border-color: transparent;
  box-shadow: 0 0 5px #2c4766;
}

.page section.help-center.home .hc-nav > nav > div {
  display: inline-block;
}

.page section.help-center.home .hc-nav > nav > div a {
  float: none;
}

.page section.main.content h2.heading,
.page section.sidebar.content h2.heading {
  line-height: 1.5;
  border-bottom: 1px solid #DADFE3;
  padding-bottom: 0.4em;
}

.page section.main.content h2.heading a:hover,
.page section.sidebar.content h2.heading a:hover {
  text-decoration: none;
}

@media (min-width: 45em) {
  .main .cs-g {
    margin-bottom: 15px;
  }
}
