// --------------------------------------------------------------------------------
// footer
// --------------------------------------------------------------------------------

footer.footer {
	background-color: #fdfdfe;
    padding: 0 50px 54px;
	border: 0;
	color: #fff;
	margin: 0;
	border-radius: 0;
	border-top:1px solid #cacaca;

	ul,li {
		list-style-type:none;
		padding:0;
		margin:0;
	}

	 nav {
		 max-width: 1000px;
		 margin: 30px auto 0;
		 height: 30px;
		 align-items: center;

		 padding-top: 10px;
		 padding-bottom: 10px;

		 max-width: 71.25rem;
		 padding-top: 10px;
		 padding-bottom: 10px;
		 min-height: 78%;

		 display:flex;
	 }

	 // @@ reset
	 a {
		 margin:0 !important;
	 }
}

// 채용공고등의 인터널 링크 아이템
footer nav {
	display: flex;

	.internal-links {
		flex:1;
		display: flex;

		li {
			line-height:15px;
			padding:0 15px;

			&:first-child {
				padding-left: 0
			}

			&:last-child {
				padding-right: 0
			}
		}
	}

	.external-links {
		display: flex;
		justify-content: flex-end;

		li {
			margin-left:23px;
		}

		a.ci {
			@include ir();

			display:inline-block;
			width:20px;
			height:20px;
			background-repeat:no-repeat;
			background-color:transparent;
			background-position:center center;
			background-size:contain;

			&.twitter {
				background-image:url('ci-twitter@2x.png');
			}
			&.facebook {
				background-image:url('ci-facebook@2x.png');
			}
			&.telegram {
				background-image:url('ci-telegram@2x.png');
			}
		}
	}
}



// 회사명
footer .company-name > div {
    max-width: 71.25rem;
    margin: 30px auto 0;
}

footer .administrative-info {
    max-width: 71.25rem;
    margin: 7px auto 0;
}

footer .copyright,footer .person-info {
    max-width: 71.25rem;
    margin: 6px auto 0;
}

footer .info-items {
    display: flex;

	li {
		padding: 0 15px;

		&:first-child {
			padding-left: 0;
		}

		&:last-child {
			padding-right: 0;
		}
	}
}


footer nav .links {
    cursor: pointer;
}

footer nav .internal-links li {
    font-size: 13px;
    border-right: 1px solid #cacaca;
}

footer nav .internal-links li,
footer nav .internal-links li a,
footer nav .internal-links li a:visited {
    letter-spacing: -1px;
    color: #666
}

footer nav .internal-links li a:active,
footer nav .internal-links li a:hover {
    color: #222;
    letter-spacing: -1px;
    text-decoration: underline
}

footer nav .internal-links li:last-child {
    border-right: none
}

footer .company-name a,footer .company-name a:visited {
    font-size: 13px;
    font-weight: 700;
    color: #777;
    letter-spacing: -1px
}

footer .info-items li {
    color: #777;
    font-size: 11px;
    border-right: 1px solid #cacaca;
    letter-spacing: -.8px;
}

footer .info-items li:last-child {
    border-right: none
}

footer .copyright {
    font-size: 12px;
    color: #888
}

.footer {
    background-color: #fff
}



@media (min-width: 45em) {
	.footer-links .privacy-link {
		float: right;
		margin-left: 25px
	}
	.footer-links .cookie-link {
		float: right
	}
	.footer-links .cookie-link:before {
		content: ""
	}

}

@media (max-width:45em) {
	.footer {
		margin-top: 0;
		padding: 1em .5em
	}
	.footer a {
		padding-left: 10px;
		padding: 0 10px;
		margin-right: 4px;
		margin-left: 4px;
		line-height: 24px;
		display: inline-block;
		margin-bottom: 10px
	}
	.footer a,
	.footer a:hover {
		color: #183247
	}
	.footer .footer-links a {
	}
	.footer-links {
		text-align: center
	}
	.footer-links a:before {
		content: none
	}
}

// copyright
footer .copyright {
	padding:0;					// @@@@ reset
	background:transparent;		// @@@@ reset
	text-align:left;
}
